  
  /* main page styling */
* {
  font-family: 'Encode Sans Expanded', sans-serif;
}
html {
  height: 100%;
}
body {
  margin: 0;
}
.App {
  display: flex;
  flex-direction: column;
}

 /* header animation styling */
.bg {
  animation:slide 3s ease-in-out infinite alternate;
  background-image: linear-gradient(-60deg,  rgb(48, 14, 63) 50%, rgb(157, 35, 173) 50%);
  height: 50vh;
  left:-50%;
  opacity:.8;
  position:fixed;
  right:-50%;
  z-index:-1;
}
.bg2 {
  animation-direction:alternate-reverse;
  animation-duration:4s;
}  
.bg3 {
  animation-duration:5s;
}
@keyframes slide {
  0% {
    transform:translateX(-25%);
  }
  100% {
    transform:translateX(25%);
  }
}

 /* header title & animation styling */
