.headline {
  position: fixed;
  top: 15vh;
  left: 2%;
  width: 32vw;
}
.headline h2 {
  -webkit-animation:shadow-pop-bl 2s cubic-bezier(.68,-.55,.265,1.55) both;animation:shadow-pop-bl 2s cubic-bezier(.68,-.55,.265,1.55) both;
  padding-left: 2vh;
  padding: 2vh;
  color: white;
  font-size: 2.5em;
}

